import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { 
  AppBar, 
  Toolbar, 
  Button, 
  Box, 
  Typography, 
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  useTheme,
  useMediaQuery
} from '@mui/material';
import {
  Dashboard as DashboardIcon,
  Store as StoreIcon,
  PointOfSale as POSIcon,
  Receipt as ReceiptIcon,
  Description as EInvoicingIcon,
  Menu as MenuIcon,
  AccountCircle as AccountIcon
} from '@mui/icons-material';
import { preloadService } from '../services/PreloadService';

// Navigation-Items mit Preloading-Konfiguration
const navigationItems = [
  {
    path: '/dashboard',
    label: 'Dashboard',
    icon: DashboardIcon,
    priority: 'critical'
  },
  {
    path: '/streckengeschaeft',
    label: 'Streckengesch√§ft',
    icon: StoreIcon,
    priority: 'high'
  },
  {
    path: '/pos',
    label: 'POS-System',
    icon: POSIcon,
    priority: 'high'
  },
  {
    path: '/daily-report',
    label: 'Tagesbericht',
    icon: ReceiptIcon,
    priority: 'medium'
  },
  {
    path: '/e-invoicing',
    label: 'E-Invoicing',
    icon: EInvoicingIcon,
    priority: 'medium'
  }
];

// Navigation Link mit Preloading
interface NavigationLinkProps {
  to: string;
  label: string;
  icon: React.ComponentType<any>;
  isActive: boolean;
  onClick?: () => void;
}

const NavigationLink: React.FC<NavigationLinkProps> = ({ 
  to, 
  label, 
  icon: Icon, 
  isActive, 
  onClick 
}) => {
  const handleMouseEnter = () => {
    // Preload Route beim Hover
    preloadService.preloadRoute(to);
  };

  return (
    <Button
      component={Link}
      to={to}
      startIcon={<Icon />}
      onClick={onClick}
      onMouseEnter={handleMouseEnter}
      data-route={to}
      sx={{
        color: isActive ? 'primary.main' : 'inherit',
        backgroundColor: isActive ? 'rgba(25, 118, 210, 0.08)' : 'transparent',
        '&:hover': {
          backgroundColor: 'rgba(25, 118, 210, 0.12)',
          color: 'primary.main'
        },
        textTransform: 'none',
        fontWeight: isActive ? 600 : 400,
        minWidth: 'auto',
        px: 2,
        py: 1
      }}
    >
      {label}
    </Button>
  );
};

// Mobile Navigation Item
interface MobileNavigationItemProps {
  to: string;
  label: string;
  icon: React.ComponentType<any>;
  isActive: boolean;
  onClick: () => void;
}

const MobileNavigationItem: React.FC<MobileNavigationItemProps> = ({ 
  to, 
  label, 
  icon: Icon, 
  isActive, 
  onClick 
}) => {
  const handleMouseEnter = () => {
    preloadService.preloadRoute(to);
  };

  return (
    <ListItem
      button
      component={Link}
      to={to}
      onClick={onClick}
      onMouseEnter={handleMouseEnter}
      data-route={to}
      sx={{
        backgroundColor: isActive ? 'rgba(25, 118, 210, 0.08)' : 'transparent',
        '&:hover': {
          backgroundColor: 'rgba(25, 118, 210, 0.12)'
        }
      }}
    >
      <ListItemIcon>
        <Icon color={isActive ? 'primary' : 'inherit'} />
      </ListItemIcon>
      <ListItemText 
        primary={label} 
        sx={{ 
          fontWeight: isActive ? 600 : 400,
          color: isActive ? 'primary.main' : 'inherit'
        }} 
      />
    </ListItem>
  );
};

// Main Navigation Component
interface NavigationProps {
  isAuthenticated: boolean;
  onLogout?: () => void;
}

export const Navigation: React.FC<NavigationProps> = ({ 
  isAuthenticated, 
  onLogout 
}) => {
  const location = useLocation();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [mobileOpen, setMobileOpen] = React.useState(false);

  // Preload kritische Routen beim Mount
  React.useEffect(() => {
    if (isAuthenticated) {
      preloadService.preloadCriticalRoutes();
    }
  }, [isAuthenticated]);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleNavigationClick = () => {
    if (isMobile) {
      setMobileOpen(false);
    }
  };

  if (!isAuthenticated) {
    return null;
  }

  return (
    <>
      {/* Desktop Navigation */}
      <AppBar 
        position="static" 
        elevation={1}
        sx={{ 
          backgroundColor: 'white',
          color: 'text.primary',
          borderBottom: '1px solid',
          borderColor: 'divider'
        }}
      >
        <Toolbar sx={{ justifyContent: 'space-between' }}>
          {/* Logo und Titel */}
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                fontWeight: 600,
                color: 'primary.main',
                mr: 4
              }}
            >
              VALEO NeuroERP
            </Typography>
          </Box>

          {/* Desktop Navigation Links */}
          {!isMobile && (
            <Box sx={{ display: 'flex', gap: 1 }}>
              {navigationItems.map((item) => (
                <NavigationLink
                  key={item.path}
                  to={item.path}
                  label={item.label}
                  icon={item.icon}
                  isActive={location.pathname === item.path}
                />
              ))}
            </Box>
          )}

          {/* Mobile Menu Button */}
          {isMobile && (
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
          )}

          {/* User Menu */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <IconButton color="inherit">
              <AccountIcon />
            </IconButton>
            {onLogout && (
              <Button
                variant="outlined"
                size="small"
                onClick={onLogout}
                sx={{ textTransform: 'none' }}
              >
                Abmelden
              </Button>
            )}
          </Box>
        </Toolbar>
      </AppBar>

      {/* Mobile Navigation Drawer */}
      <Drawer
        variant="temporary"
        anchor="left"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true // Bessere Performance auf Mobile
        }}
        sx={{
          display: { xs: 'block', md: 'none' },
          '& .MuiDrawer-paper': { 
            boxSizing: 'border-box', 
            width: 280 
          }
        }}
      >
        <Box sx={{ p: 2 }}>
          <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>
            Navigation
          </Typography>
          <List>
            {navigationItems.map((item) => (
              <MobileNavigationItem
                key={item.path}
                to={item.path}
                label={item.label}
                icon={item.icon}
                isActive={location.pathname === item.path}
                onClick={handleNavigationClick}
              />
            ))}
          </List>
        </Box>
      </Drawer>
    </>
  );
};

// Preload-Status Indicator (nur in Entwicklung)
export const PreloadIndicator: React.FC = () => {
  const [preloadStatus, setPreloadStatus] = React.useState<Record<string, boolean>>({});

  React.useEffect(() => {
    const updateStatus = () => {
      setPreloadStatus(preloadService.getPreloadStatus());
    };

    updateStatus();
    const interval = setInterval(updateStatus, 2000);

    return () => clearInterval(interval);
  }, []);

  if (process.env.NODE_ENV !== 'development') return null;

  const loadedCount = Object.values(preloadStatus).filter(Boolean).length;
  const totalCount = Object.keys(preloadStatus).length;

  return (
    <Box
      sx={{
        position: 'fixed',
        top: 16,
        right: 16,
        bgcolor: 'rgba(0,0,0,0.8)',
        color: 'white',
        p: 1,
        borderRadius: 1,
        fontSize: '0.75rem',
        zIndex: 9999
      }}
    >
      üîÑ {loadedCount}/{totalCount} Routen preloaded
    </Box>
  );
}; 